body {
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

:root {
    --dark: rgb(34, 40, 49);
    --semi: rgb(57, 62, 70);
    /* --semi: rgb(68, 68, 68); */
    --skyblue: rgb(0, 173, 181);
    --clear: rgb(238, 238, 238);
    
    /* --dark: rgb(23, 23, 23);
    --semi: rgb(68, 68, 68);
    --skyblue: rgb(218, 0, 55);
    --clear: rgb(237, 237, 237); */
}

.App {
    display: flex;
    flex-direction: column;
    color: var(--dark);
    background-color: var(--clear);
    font-family: 'Montserrat', sans-serif;
}

a {
    text-decoration: none;
}

.section {
    margin-bottom: 35px;
    min-height: 100vh;
    padding: 0 15%;
    text-align: center;
}

.navbar {
    background-color: var(--semi);
    color: var(--clear);
    position: fixed;
    top: 0;
    margin: 0;
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 45px;
}

.navbar a {
    color: var(--clear);
    margin-right: 15px;
    transition: 0.3s;
}

.navbar a:hover {
    color: var(--skyblue);
}

#about {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100vh;
}

.material-icons {
    color: var(--semi);
    font-size: 250px;
}

.about__colums{
    width: 400px;
    margin: 0 5%;
    /* min-height: 240px; */
}

#about__info {
    font-size: 1.3em;
    display: flex;
    flex-direction: column;
}

#about__info h2 {
    margin-top: 0;
}

#about__info h2,
#about__info h3 {
    margin: 0 0 20px 0;
    font-size: 30px;
    text-align: left;
}

#about__info span {
    text-align: justify;
}

#skills h1,
#projects h1,
#contact h1 {
    font-size: 3em;
    margin-top: 40px;
}

.skill-item {
    background-color: var(--semi);
    color: var(--clear);
    width: 320px;
    border-radius: 15px;
    padding: 5px 0;
    margin-bottom: 15px;
    box-shadow: 2px 8px 10px rgba(151, 151, 255, 0.2);
    transition: 0.3s;
}

#skills-table {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding-top: 15px;
}

.skill-item__table {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.skill-item__table div {
    width: 40%;
    margin-top: 5px;
    transition: 0.3s;
}

.skill-item__table div:hover {
    color: var(--skyblue);
}

.skill-item__table img {
    height: 48px;
    width: 48px;
}

.skill-item:hover,
.project-item:hover,
#contact-info:hover {
    box-shadow: 6px 12px 12px rgba(0, 0, 255, 0.2);
}

.project-item{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 15px;
    background-color: var(--clear);
    border-radius: 15px;
    padding: 10px 0;
    margin: 15px 0;
    box-shadow: 2px 8px 10px rgba(151, 151, 255, 0.2);
    transition: 0.3s;
}

.project-item {
    justify-content: center;
}

.project-item div {
    width: 531px;
}

.project-item img {
    width: 80%;
}

.project-item h2 {
    font-size: 2em;
    margin: 0;
}

.project-item__info {
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.project-item__info div {
    width: 80%;
}

.project-item__info a{
    margin: 20px 10px;
    text-decoration: none;
    padding: 6px 12px;
    border-radius: 20px;
}

.project-item__links {
    margin: 15px 0;
}

.demo-link {
    border: var(--skyblue) solid 3px;
    color: var(--skyblue);
    transition: 0.3s;
}

.demo-link:hover {
    border: var(--skyblue) solid 3px;
    background-color: var(--skyblue);
    color: var(--clear);
}

.repo-link {
    border: var(--semi) solid 3px;
    color: var(--semi);
    transition: 0.3s;
}

.repo-link:hover {
    border: var(--semi) solid 3px;
    background-color: var(--semi);
    color: var(--clear);
}

.project-item__stack {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 15px;
    align-items: flex-start;
}

.tool {
    display: flex;
    flex: 0;
    color: var(--clear);
    background-color: var(--semi);
    border-radius: 15px;
    padding: 0 5px;
    transition: 0.3s;
    margin: 10px 0 0 4px;
}

.project-item__stack p {
    margin: 10px 0 0 4px;
}

.tool p {
    margin: 0;
}

.tool:hover {
    color: var(--skyblue);
}

.project-item__stack img{
    width: 18px;
    height: 18px;
    margin: 0 3px;
}

#contact {
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#contact-info {
    background-color: var(--semi);
    border-radius: 25px;
    color: var(--clear);
    padding: 6% 0;
    width: 50%;
    box-shadow: 2px 8px 10px rgba(151, 151, 255, 0.2);
    transition: 0.3s;
}

#contact__links {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    min-height: 70px;
}

#contact__links img{
    transition: 0.3s;
}

#contact__links img:hover {
    height: 100%;
    margin: 0 8% 10% 8%;
}

#contact__email {
    display: flex;
    align-items: center;
    justify-content: center;
}

#contact__email h2, 
#contact__email img {
    margin: 0 3%;    
}

#navbar__control {
    display: none;
    margin: 0 35px 0 0;
    background-color: var(--semi);
    color: var(--clear);
}

@media screen and (max-width: 600px) {
    .navbar a {
        display: none;
    }
    #navbar__control {
        float: right;   
        display: block;
    }
}

@media screen and (max-width: 600px) {
    .navbar.responsive {
        display: flex;
        flex-direction: column;
        position: fixed;
        width: 100%;
        background-color: var(--semi);
        color: var(--clear);
        justify-content: flex-end;
        align-items: flex-end;
        transition: 0.3s;
        height: auto;
    }
    .navbar.responsive * {
        display: flex;
        margin: 5px 35px 5px 0;
        color: var(--clear);
    }

    .navbar.responsive a:hover,
    #navbar__control:hover {
        color: var(--skyblue);
    }
}